const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "1",
    other: "{{count}}",
    oneWithSuffix: "1",
    otherWithSuffix: "{{count}}",
  },

  xSeconds: {
    one: "1",
    other: "{{count}}",
  },

  halfAMinute: "30",

  lessThanXMinutes: {
    one: "1",
    other: "{{count}}",
    oneWithSuffix: "1",
    otherWithSuffix: "{{count}}",
  },

  xMinutes: {
    one: "1",
    other: "{{count}}",
  },

  aboutXHours: {
    one: "1",
    other: "{{count}}",
  },

  xHours: {
    one: "1",
    other: "{{count}}",
  },

  xDays: {
    one: "1",
    other: "{{count}}",
  },

  aboutXWeeks: {
    one: "1",
    other: "{{count}}",
  },

  xWeeks: {
    one: "1",
    other: "{{count}}",
  },

  aboutXMonths: {
    one: "1",
    other: "{{count}}",
  },

  xMonths: {
    one: "1",
    other: "{{count}}",
  },

  aboutXYears: {
    one: "1",
    other: "{{count}}",
  },

  xYears: {
    one: "1",
    other: "{{count}}",
  },

  overXYears: {
    one: "1",
    other: "{{count}}",
  },

  almostXYears: {
    one: "1",
    other: "{{count}}",
  },
};

export const formatDistance = (token, count, options) => {
  options = options || {};

  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    if (options.addSuffix && tokenValue.oneWithSuffix) {
      result = tokenValue.oneWithSuffix;
    } else {
      result = tokenValue.one;
    }
  } else {
    if (options.addSuffix && tokenValue.otherWithSuffix) {
      result = tokenValue.otherWithSuffix.replace("{{count}}", String(count));
    } else {
      result = tokenValue.other.replace("{{count}}", String(count));
    }
  }

  if (options.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + "";
    } else {
      return result + "";
    }
  }

  return result;
};
