import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { changePassword } from '../utils/api';
import './DetailPage.css'; //  

interface ChangePasswordPageProps {
  currentUser: any;
  onChangePassword: (newPassword: string) => boolean;
}

const ChangePasswordPage: React.FC<ChangePasswordPageProps> = ({ currentUser, onChangePassword }) => {
  const navigate = useNavigate();
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    //      
    if (!currentUser) {
      navigate('/');
    }
  }, [currentUser, navigate]);

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    
    if (newPassword.length < 6) {
      setMessage('  6  .');
      return;
    }
    
    if (newPassword !== confirmPassword) {
      setMessage('  .');
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      //       
      const currentPwd = currentUser.isTemporaryPassword ? '' : currentPassword;
      
      //    
      await changePassword(currentPwd, newPassword);
      
      //   
      onChangePassword(newPassword);
      
      setMessage('  !');
      setTimeout(() => {
        navigate('/');
      }, 2000);
    } catch (error: any) {
      setMessage(error.message || '    .');
    } finally {
      setIsLoading(false);
    }
  };

  //     
  if (!currentUser) {
    return null;
  }

  return (
    <div className="form-container-wrapper">
      <h1> </h1>
      {currentUser.isTemporaryPassword && (
        <div style={{ 
          backgroundColor: '#fff3cd', 
          border: '1px solid #ffeaa7', 
          borderRadius: '8px', 
          padding: '16px', 
          marginBottom: '20px' 
        }}>
          <p style={{ margin: 0, color: '#856404' }}>
            <strong>    .</strong>
          </p>
        </div>
      )}
      
      <form onSubmit={handleSubmit} className="form-container">
        {!currentUser.isTemporaryPassword && (
          <div className="form-group">
            <label htmlFor="current-password"> </label>
            <input
              type="password"
              id="current-password"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              placeholder="  "
              required
            />
          </div>
        )}
        
        <div className="form-group">
          <label htmlFor="new-password"> </label>
          <input
            type="password"
            id="new-password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            placeholder=" 6 "
            required
            minLength={6}
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="confirm-password">  </label>
          <input
            type="password"
            id="confirm-password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            placeholder="  "
            required
          />
        </div>
        
        <button type="submit" className="submit-button" disabled={isLoading}>
          {isLoading ? ' ...' : ' '}
        </button>
      </form>

      {message && (
        <div className="reset-message">
          <p>{message}</p>
        </div>
      )}
    </div>
  );
};

export default ChangePasswordPage; 