import React from 'react';
import ReactDOM from 'react-dom/client';
import { HelmetProvider } from 'react-helmet-async';
import './index.css';
import './i18n/config'; // i18n 
import App from './App';
import reportWebVitals from './reportWebVitals';
import ErrorBoundary from './components/ErrorBoundary';

console.log(' React   - index.tsx ');

const rootElement = document.getElementById('root');
if (!rootElement) {
  console.error(' root    !');
  throw new Error('root    .');
}

console.log(' root  :', rootElement);

const root = ReactDOM.createRoot(rootElement);

try {
  console.log(' React   ...');
  root.render(
    <React.StrictMode>
      <ErrorBoundary>
        <HelmetProvider>
          <App />
        </HelmetProvider>
      </ErrorBoundary>
    </React.StrictMode>
  );
  console.log(' React   ');
  
  //   DOM 
  setTimeout(() => {
    const rootContent = rootElement.innerHTML;
    const rootText = rootElement.innerText || rootElement.textContent || '';
    console.log(' DOM  :');
    console.log('  - root children :', rootElement.children.length);
    console.log('  - root innerText :', rootText.length);
    console.log('  - root innerHTML :', rootContent.length);
    if (rootText.length === 0) {
      console.error(' root  !');
    }
  }, 1000);
} catch (error) {
  console.error(' React   :', error);
  rootElement.innerHTML = `
    <div style="padding: 20px; text-align: center; color: #333; background: #fff; min-height: 100vh; display: flex; flex-direction: column; justify-content: center; align-items: center;">
      <h2 style="color: #667eea; font-size: 24px; margin-bottom: 16px;"> </h2>
      <p style="margin-bottom: 20px; font-size: 16px;">${error instanceof Error ? error.message : '   '}</p>
      <button onclick="window.location.reload()" style="padding: 12px 24px; background: #667eea; color: white; border: none; border-radius: 8px; cursor: pointer; font-size: 16px; font-weight: 600;"></button>
    </div>
  `;
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
