import React, { useState } from 'react';
import './DetailPage.css'; //  

interface ForgotPasswordPageProps {
  onVerifyAndResetPassword: (phone: string, code: string) => Promise<string | null>;
  onSendVerificationCode: (phone: string) => Promise<boolean>;
  onRequestPasswordResetByEmail: (email: string) => Promise<boolean>;
  onRequestPasswordResetBySMS: (phone: string) => Promise<boolean>;
  theme: 'light' | 'dark';
}

type ResetMethod = 'email' | 'phone';

//    
const validateEmail = (email: string): { isValid: boolean; error: string } => {
  if (!email) {
    return { isValid: false, error: '  .' };
  }
  
  if (!email.includes('@')) {
    return { isValid: false, error: '  @  .' };
  }
  
  if (!email.includes('.')) {
    return { isValid: false, error: '    .' };
  }
  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (!emailRegex.test(email)) {
    return { isValid: false, error: '    .' };
  }
  
  return { isValid: true, error: '' };
};

const ForgotPasswordPage = ({ 
  onVerifyAndResetPassword, 
  onSendVerificationCode,
  onRequestPasswordResetByEmail,
  onRequestPasswordResetBySMS,
  theme
}: ForgotPasswordPageProps) => {
  const [resetMethod, setResetMethod] = useState<ResetMethod>('email');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [code, setCode] = useState('');
  
  const [isCodeSent, setIsCodeSent] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isEmailSent, setIsEmailSent] = useState(false);
  const [isSMSSent, setIsSMSSent] = useState(false);
  
  const [message, setMessage] = useState('');
  const [tempPassword, setTempPassword] = useState('');
  const [emailError, setEmailError] = useState('');

  //     
  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newEmail = e.target.value;
    setEmail(newEmail);
    
    if (newEmail) {
      const validation = validateEmail(newEmail);
      setEmailError(validation.error);
    } else {
      setEmailError('');
    }
  };

  const handleSendCode = async () => {
    setIsLoading(true);
    setMessage('');
    
    const success = await onSendVerificationCode(phone);
    
    setTimeout(() => {
      if (success) {
        setMessage(' 6 !     .');
        setIsCodeSent(true);
      } else {
        setMessage('   .');
      }
      setIsLoading(false);
    }, 1000);
  };

  const handleSendEmail = async () => {
    setIsLoading(true);
    setMessage('');
    
    try {
      const success = await onRequestPasswordResetByEmail(email);
      if (success) {
        setMessage('   !   .');
        setIsEmailSent(true);
      } else {
        setMessage('   .');
      }
    } catch (error: any) {
      setMessage(error.message || '  .');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSendSMS = async () => {
    setIsLoading(true);
    setMessage('');
    
    try {
      const success = await onRequestPasswordResetBySMS(phone);
      if (success) {
        setMessage('  SMS !  SMS .');
        setIsSMSSent(true);
      } else {
        setMessage('   .');
      }
    } catch (error: any) {
      setMessage(error.message || 'SMS  .');
    } finally {
      setIsLoading(false);
    }
  };

  const handleVerifyAndReset = async (event: React.FormEvent) => {
    event.preventDefault();
    
    //   
    if (!code || code.length !== 6) {
      setMessage('  6  .');
      return;
    }
    
    if (!/^\d{6}$/.test(code)) {
      setMessage('    .');
      return;
    }
    
    const newTempPassword = await onVerifyAndResetPassword(phone, code);

    if (newTempPassword) {
      setMessage('    .     .');
      setTempPassword(newTempPassword);
      setIsCodeSent(false); //   
    } else {
      setMessage('  .  .');
      setTempPassword('');
    }
  };

  const resetForm = () => {
    setEmail('');
    setPhone('');
    setCode('');
    setIsCodeSent(false);
    setIsEmailSent(false);
    setIsSMSSent(false);
    setMessage('');
    setTempPassword('');
  };

  return (
    <div className="form-container-wrapper" style={{ 
      minHeight: '100vh', 
      display: 'flex', 
      flexDirection: 'column',
      justifyContent: 'center',
      padding: '20px'
    }}>
      <h1 style={{ marginBottom: '16px', fontSize: 'min(24px, 6vw)' }}>  </h1>
      
      {!tempPassword && !isEmailSent ? (
        <>
          <div className="reset-method-selector" style={{ marginBottom: '16px' }}>
            <label style={{ display: 'block', marginBottom: '8px', fontWeight: 'bold', fontSize: 'min(14px, 3.5vw)' }}>
                :
            </label>
            <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
              <label style={{ display: 'flex', alignItems: 'center', gap: '4px', fontSize: 'min(13px, 3.2vw)' }}>
                <input
                  type="radio"
                  name="resetMethod"
                  value="email"
                  checked={resetMethod === 'email'}
                  onChange={(e) => {
                    setResetMethod(e.target.value as ResetMethod);
                    resetForm();
                  }}
                />
                  
              </label>
              <label style={{ display: 'flex', alignItems: 'center', gap: '4px', fontSize: 'min(13px, 3.2vw)' }}>
                <input
                  type="radio"
                  name="resetMethod"
                  value="phone"
                  checked={resetMethod === 'phone'}
                  onChange={(e) => {
                    setResetMethod(e.target.value as ResetMethod);
                    resetForm();
                  }}
                />
                 SMS 
              </label>
            </div>
          </div>

          {resetMethod === 'email' ? (
            <div className="form-container" style={{ marginBottom: '16px' }}>
              <div className="form-group" style={{ marginBottom: '12px' }}>
                <label htmlFor="email" style={{ fontSize: 'min(14px, 3.5vw)', marginBottom: '6px' }}>  </label>
                <input
                  type="email"
                  id="email"
                  value={email}
                  onChange={handleEmailChange}
                  placeholder="   "
                  required
                  style={{ padding: '8px 12px', fontSize: 'min(14px, 3.5vw)' }}
                />
                {emailError && <p style={{ color: 'red', fontSize: 'min(12px, 3vw)', marginTop: '4px', marginBottom: '0' }}>{emailError}</p>}
              </div>
              
              <button 
                type="button" 
                onClick={handleSendEmail} 
                disabled={isLoading || !email || !!emailError}
                className="submit-button"
                style={{ 
                  padding: '10px 16px', 
                  fontSize: 'min(14px, 3.5vw)',
                  marginTop: '8px'
                }}
              >
                {isLoading ? ' ...' : '   '}
              </button>
            </div>
          ) : (
            <div className="form-container" style={{ marginBottom: '16px' }}>
              <div className="form-group" style={{ marginBottom: '12px' }}>
                <label htmlFor="phone" style={{ fontSize: 'min(14px, 3.5vw)', marginBottom: '6px' }}>  </label>
                <input
                  type="tel"
                  id="phone"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="'-'   "
                  required
                  style={{ padding: '8px 12px', fontSize: 'min(14px, 3.5vw)' }}
                />
              </div>
              
              <button 
                type="button" 
                onClick={handleSendSMS} 
                disabled={isLoading || !phone}
                className="submit-button"
                style={{ 
                  padding: '10px 16px', 
                  fontSize: 'min(14px, 3.5vw)',
                  marginTop: '8px'
                }}
              >
                {isLoading ? ' ...' : '  '}
              </button>
            </div>
          )}
        </>
      ) : null}

      {message && (
        <div className="reset-message" style={{ marginBottom: '16px' }}>
          <p style={{ fontSize: 'min(14px, 3.5vw)', marginBottom: '8px' }}>{message}</p>
          {tempPassword && (
            <div className="temp-password-display">
              <strong style={{ fontSize: 'min(14px, 3.5vw)' }}> : <span className="password-text">{tempPassword}</span></strong>
              <div style={{ 
                backgroundColor: '#fff3cd', 
                border: '1px solid #ffeaa7', 
                borderRadius: '8px', 
                padding: '10px', 
                marginTop: '8px' 
              }}>
                <p style={{ margin: 0, color: '#856404', fontSize: 'min(12px, 3vw)' }}>
                   <strong> :</strong><br />
                       <br />
                        <br />
                      
                </p>
              </div>
            </div>
          )}
          {isEmailSent && (
            <div style={{ 
              backgroundColor: '#d4edda', 
              border: '1px solid #c3e6cb', 
              borderRadius: '8px', 
              padding: '12px', 
              marginTop: '12px' 
            }}>
              <p style={{ margin: 0, color: '#155724', fontSize: 'min(13px, 3.2vw)' }}>
                <strong>  !</strong><br />
                     .<br />
                        .
              </p>
            </div>
          )}
          {isSMSSent && (
            <div style={{ 
              backgroundColor: '#d4edda', 
              border: '1px solid #c3e6cb', 
              borderRadius: '8px', 
              padding: '12px', 
              marginTop: '12px' 
            }}>
              <p style={{ margin: 0, color: '#155724', fontSize: 'min(13px, 3.2vw)' }}>
                <strong> SMS !</strong><br />
                 SMS    .<br />
                        .
              </p>
            </div>
          )}
        </div>
      )}

      <div style={{ marginTop: '16px', textAlign: 'center' }}>
        <button 
          onClick={resetForm}
          style={{ 
            background: 'none', 
            border: '1px solid #007bff', 
            color: '#007bff', 
            padding: '8px 16px', 
            borderRadius: '4px', 
            cursor: 'pointer',
            fontSize: 'min(14px, 3.5vw)'
          }}
        >
            
        </button>
      </div>
    </div>
  );
};

const ForgotPasswordPageComponent = ForgotPasswordPage;
export default ForgotPasswordPageComponent; 