import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import Backend from 'i18next-http-backend';

console.log(' i18n  ');

//    (fallback)
const defaultResources = {
  ko: {
    translation: {
      mainPage: {
        title: '  ',
        description: '   !',
        mapButton: ' ',
        registerButton: '',
        listButton: ' '
      }
    }
  }
};

// i18n  -  
try {
  i18n
    .use(Backend)
    .use(initReactI18next)
    .init({
      resources: defaultResources,
      backend: {
        loadPath: './locales/{{lng}}/{{ns}}.json',
        allowMultiLoading: false,
      },
      lng: 'ko', //  
      fallbackLng: 'ko',
      debug: false,
      interpolation: {
        escapeValue: false,
      },
      react: {
        useSuspense: false,
      },
      //        
      partialBundledLanguages: true,
    })
    .then(() => {
      console.log(' i18n  ');
    })
    .catch((error: any) => {
      console.error(' i18n initialization error:', error);
      //      
      i18n.init({
        resources: defaultResources,
        lng: 'ko',
        fallbackLng: 'ko',
        interpolation: {
          escapeValue: false,
        },
      });
    });
} catch (error) {
  console.error(' i18n setup error:', error);
  //   
  i18n.init({
    resources: defaultResources,
    lng: 'ko',
    fallbackLng: 'ko',
    interpolation: {
      escapeValue: false,
    },
  });
}

export default i18n;

