import React, { useEffect, useRef, useState } from 'react';

export interface MarkerData {
  id: number | string;
  position: { lat: number; lng: number };
  title: string;
  description?: string;
  isHighlighted?: boolean;
  isMyItem?: boolean;
}

interface NaverMapComponentProps {
  center?: { lat: number; lng: number };
  zoom?: number;
  markers?: MarkerData[];
  onMarkerClick?: (markerId: number | string) => void;
  onMapClick?: (lat: number, lng: number) => void;
  selectedMarkerId?: number | string | null;
  showSearchMarker?: boolean;
  searchMarkerPosition?: { lat: number; lng: number } | null;
  height?: string;
  className?: string;
}

declare global {
  interface Window {
    naver: any;
  }
}

const NaverMapComponent: React.FC<NaverMapComponentProps> = ({
  center = { lat: 37.5665, lng: 126.9780 }, //  
  zoom = 15,
  markers = [],
  onMarkerClick,
  onMapClick,
  selectedMarkerId,
  showSearchMarker = false,
  searchMarkerPosition,
  height = '500px',
  className = '',
}) => {
  const mapRef = useRef<HTMLDivElement>(null);
  const [map, setMap] = useState<any>(null);
  const [mapLoaded, setMapLoaded] = useState(false);
  const [error, setError] = useState<string>('');
  const markersRef = useRef<Map<number | string, any>>(new Map());
  const searchMarkerRef = useRef<any>(null);
  const infoWindowsRef = useRef<Map<number | string, any>>(new Map());

  //   API 
  useEffect(() => {
    const apiKey = process.env.REACT_APP_NAVER_MAPS_API_KEY;
    
    if (!apiKey) {
      setError('  API   .  REACT_APP_NAVER_MAPS_API_KEY .');
      console.error(' REACT_APP_NAVER_MAPS_API_KEY  .');
      return;
    }

    //    
    if (window.naver && window.naver.maps) {
      setMapLoaded(true);
      return;
    }

    //   API  
    const script = document.createElement('script');
    script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${apiKey}`;
    script.async = true;
    script.onload = () => {
      console.log('   API  ');
      setMapLoaded(true);
    };
    script.onerror = () => {
      console.error('   API  ');
      setError('    .   .');
    };
    document.head.appendChild(script);

    return () => {
      // 
      if (script.parentNode) {
        script.parentNode.removeChild(script);
      }
    };
  }, []);

  //  
  useEffect(() => {
    if (!mapLoaded || !mapRef.current || map || !window.naver || !window.naver.maps) return;

    try {
      const location = new window.naver.maps.LatLng(center.lat, center.lng);
      const mapOptions = {
        center: location,
        zoom: zoom,
        zoomControl: true,
        zoomControlOptions: {
          position: window.naver.maps.Position.TOP_RIGHT,
        },
      };

      const newMap = new window.naver.maps.Map(mapRef.current, mapOptions);

      //   
      if (onMapClick) {
        window.naver.maps.Event.addListener(newMap, 'click', (e: any) => {
          const lat = e.coord.lat();
          const lng = e.coord.lng();
          onMapClick(lat, lng);
        });
      }

      setMap(newMap);
      console.log('    ');
    } catch (err) {
      console.error('    :', err);
      setError('   .');
    }
  }, [mapLoaded, map, center, zoom, onMapClick]);

  //  
  useEffect(() => {
    if (map && center && window.naver && window.naver.maps) {
      const location = new window.naver.maps.LatLng(center.lat, center.lng);
      map.setCenter(location);
    }
  }, [map, center]);

  //  
  useEffect(() => {
    if (map && zoom) {
      map.setZoom(zoom);
    }
  }, [map, zoom]);

  //   
  useEffect(() => {
    if (!map || !window.naver || !window.naver.maps) return;

    //    
    if (searchMarkerRef.current) {
      searchMarkerRef.current.setMap(null);
      searchMarkerRef.current = null;
    }

    //    
    if (showSearchMarker && searchMarkerPosition) {
      const marker = new window.naver.maps.Marker({
        position: new window.naver.maps.LatLng(searchMarkerPosition.lat, searchMarkerPosition.lng),
        map: map,
        title: ' ',
        icon: {
          url: 'https://maps.google.com/mapfiles/ms/icons/blue-dot.png',
          size: new window.naver.maps.Size(40, 40),
          scaledSize: new window.naver.maps.Size(40, 40),
        },
      });

      searchMarkerRef.current = marker;
    }
  }, [map, showSearchMarker, searchMarkerPosition]);

  //  
  useEffect(() => {
    if (!map || !window.naver || !window.naver.maps) return;

    //   
    markersRef.current.forEach((marker) => marker.setMap(null));
    markersRef.current.clear();
    infoWindowsRef.current.forEach((infoWindow) => infoWindow.close());
    infoWindowsRef.current.clear();

    //   
    markers.forEach((markerData) => {
      const isSelected = selectedMarkerId === markerData.id;
      const isMyItem = markerData.isMyItem || false;

      //   
      let iconUrl = 'https://maps.google.com/mapfiles/ms/icons/red-dot.png';
      let iconSize = new window.naver.maps.Size(35, 35);

      if (isSelected) {
        iconUrl = 'https://maps.google.com/mapfiles/ms/icons/yellow-dot.png';
        iconSize = new window.naver.maps.Size(50, 50);
      } else if (isMyItem) {
        iconUrl = 'https://maps.google.com/mapfiles/ms/icons/green-dot.png';
      }

      const marker = new window.naver.maps.Marker({
        position: new window.naver.maps.LatLng(markerData.position.lat, markerData.position.lng),
        map: map,
        title: markerData.title,
        icon: {
          url: iconUrl,
          size: iconSize,
          scaledSize: iconSize,
        },
        animation: isSelected ? window.naver.maps.Animation.BOUNCE : undefined,
      });

      //   
      window.naver.maps.Event.addListener(marker, 'click', () => {
        if (onMarkerClick) {
          onMarkerClick(markerData.id);
        }
      });

      // InfoWindow ()
      if (markerData.description) {
        const infoWindow = new window.naver.maps.InfoWindow({
          content: `
            <div style="padding: 8px; max-width: 200px;">
              <h4 style="margin: 0 0 5px 0; font-size: 14px; font-weight: bold;">${markerData.title}</h4>
              <p style="margin: 0; font-size: 12px; color: #666;">${markerData.description}</p>
              ${isMyItem ? '<p style="margin: 5px 0 0 0; font-size: 11px; color: #4CAF50; font-weight: bold;">  </p>' : ''}
            </div>
          `,
        });

        window.naver.maps.Event.addListener(marker, 'click', () => {
          infoWindow.open(map, marker);
        });

        infoWindowsRef.current.set(markerData.id, infoWindow);
      }

      markersRef.current.set(markerData.id, marker);
    });
  }, [map, markers, selectedMarkerId, onMarkerClick]);

  //    
  useEffect(() => {
    if (map && selectedMarkerId && window.naver && window.naver.maps) {
      const marker = markersRef.current.get(selectedMarkerId);
      if (marker) {
        const position = marker.getPosition();
        if (position) {
          map.setCenter(position);
          map.setZoom(17);
        }
      }
    }
  }, [map, selectedMarkerId]);

  //  
  if (error) {
    return (
      <div
        style={{
          width: '100%',
          height,
          backgroundColor: '#fff3cd',
          border: '2px solid #ffeaa7',
          borderRadius: '12px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
          padding: '20px',
          color: '#856404',
        }}
      >
        <div style={{ fontSize: '32px', marginBottom: '15px' }}></div>
        <div style={{ fontSize: '16px', textAlign: 'center', fontWeight: 'bold', marginBottom: '8px' }}>
             
        </div>
        <div style={{ fontSize: '14px', textAlign: 'center', color: '#666' }}>
          {error}
        </div>
      </div>
    );
  }

  //  
  if (!mapLoaded) {
    return (
      <div
        style={{
          width: '100%',
          height,
          backgroundColor: '#f8f9fa',
          border: '2px solid #e9ecef',
          borderRadius: '12px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
        }}
      >
        <div style={{ fontSize: '32px', marginBottom: '15px' }}></div>
        <div style={{ fontSize: '16px', color: '#6c757d' }}>   ...</div>
      </div>
    );
  }

  //  
  return (
    <div
      ref={mapRef}
      className={className}
      style={{
        width: '100%',
        height,
        borderRadius: '12px',
        overflow: 'hidden',
        boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
      }}
    />
  );
};

export default NaverMapComponent;

