/**
 *   ID  
 *          
 */
export const extractCreatedId = (data: any): string | number | null => {
  const id =
    data?.item?.id ??
    data?.id ??
    data?._id ??
    data?.itemId ??
    data?.createdId ??
    data?.result?.id ??
    data?.result?._id ??
    data?.data?.id ??
    data?.data?._id;

  if (id === 0) return 0;
  if (id === undefined || id === null) return null;
  return id;
};

