import React, { useEffect, useRef, useState } from 'react';
import { Loader } from '@googlemaps/js-api-loader';

export interface MarkerData {
  id: number | string;
  position: { lat: number; lng: number };
  title: string;
  description?: string;
  isHighlighted?: boolean;
  isMyItem?: boolean;
}

interface GoogleMapComponentProps {
  center?: { lat: number; lng: number };
  zoom?: number;
  markers?: MarkerData[];
  onMarkerClick?: (markerId: number | string) => void;
  onMapClick?: (lat: number, lng: number) => void;
  selectedMarkerId?: number | string | null;
  showSearchMarker?: boolean;
  searchMarkerPosition?: { lat: number; lng: number } | null;
  height?: string;
  className?: string;
}

const GoogleMapComponent: React.FC<GoogleMapComponentProps> = ({
  center = { lat: 37.5665, lng: 126.9780 }, //  
  zoom = 15,
  markers = [],
  onMarkerClick,
  onMapClick,
  selectedMarkerId,
  showSearchMarker = false,
  searchMarkerPosition,
  height = '500px',
  className = '',
}) => {
  const mapRef = useRef<HTMLDivElement>(null);
  const [map, setMap] = useState<google.maps.Map | null>(null);
  const [mapLoaded, setMapLoaded] = useState(false);
  const [error, setError] = useState<string>('');
  const markersRef = useRef<Map<number | string, google.maps.Marker>>(new Map());
  const searchMarkerRef = useRef<google.maps.Marker | null>(null);

  // Google Maps API 
  useEffect(() => {
    const apiKey = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
    
    if (!apiKey) {
      setError(' API   . ENV_SETUP.md  API  .');
      console.error(' REACT_APP_GOOGLE_MAPS_API_KEY  .');
      return;
    }

    const loader = new Loader({
      apiKey,
      version: 'weekly',
      libraries: ['places'],
      language: 'ko',
      region: 'KR',
    });

    (loader as any)
      .load()
      .then(() => {
        console.log('  API  ');
        setMapLoaded(true);
      })
      .catch((err: any) => {
        console.error('  API  :', err);
        setError('   .   .');
      });
  }, []);

  //  
  useEffect(() => {
    if (!mapLoaded || !mapRef.current || map) return;

    try {
      const newMap = new google.maps.Map(mapRef.current, {
        center,
        zoom,
        zoomControl: true,
        mapTypeControl: false,
        streetViewControl: false,
        fullscreenControl: true,
        gestureHandling: 'greedy',
      });

      //   
      if (onMapClick) {
        newMap.addListener('click', (e: google.maps.MapMouseEvent) => {
          if (e.latLng) {
            onMapClick(e.latLng.lat(), e.latLng.lng());
          }
        });
      }

      setMap(newMap);
      console.log('   ');
    } catch (err) {
      console.error('   :', err);
      setError('   .');
    }
  }, [mapLoaded, map, center, zoom, onMapClick]);

  //  
  useEffect(() => {
    if (map && center) {
      map.setCenter(center);
    }
  }, [map, center]);

  //  
  useEffect(() => {
    if (map && zoom) {
      map.setZoom(zoom);
    }
  }, [map, zoom]);

  //    ( )
  useEffect(() => {
    if (!map) return;

    //    
    if (searchMarkerRef.current) {
      searchMarkerRef.current.setMap(null);
      searchMarkerRef.current = null;
    }

    //    
    if (showSearchMarker && searchMarkerPosition) {
      const marker = new google.maps.Marker({
        position: searchMarkerPosition,
        map,
        title: ' ',
        animation: google.maps.Animation.DROP,
        icon: {
          url: 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png',
          scaledSize: new google.maps.Size(40, 40),
        },
      });

      searchMarkerRef.current = marker;
    }
  }, [map, showSearchMarker, searchMarkerPosition]);

  //  
  useEffect(() => {
    if (!map) return;

    //   
    markersRef.current.forEach((marker) => marker.setMap(null));
    markersRef.current.clear();

    //   
    markers.forEach((markerData) => {
      const isSelected = selectedMarkerId === markerData.id;
      const isMyItem = markerData.isMyItem || false;

      //   
      let iconUrl = 'http://maps.google.com/mapfiles/ms/icons/red-dot.png';
      let scale = 35;

      if (isSelected) {
        iconUrl = 'http://maps.google.com/mapfiles/ms/icons/yellow-dot.png';
        scale = 50; //   
      } else if (isMyItem) {
        iconUrl = 'http://maps.google.com/mapfiles/ms/icons/green-dot.png';
      }

      const marker = new google.maps.Marker({
        position: markerData.position,
        map,
        title: markerData.title,
        animation: isSelected ? google.maps.Animation.BOUNCE : undefined,
        icon: {
          url: iconUrl,
          scaledSize: new google.maps.Size(scale, scale),
        },
      });

      //   
      marker.addListener('click', () => {
        if (onMarkerClick) {
          onMarkerClick(markerData.id);
        }
      });

      // InfoWindow ()
      if (markerData.description) {
        const infoWindow = new google.maps.InfoWindow({
          content: `
            <div style="padding: 8px; max-width: 200px;">
              <h4 style="margin: 0 0 5px 0; font-size: 14px; font-weight: bold;">${markerData.title}</h4>
              <p style="margin: 0; font-size: 12px; color: #666;">${markerData.description}</p>
              ${isMyItem ? '<p style="margin: 5px 0 0 0; font-size: 11px; color: #4CAF50; font-weight: bold;">  </p>' : ''}
            </div>
          `,
        });

        marker.addListener('click', () => {
          infoWindow.open(map, marker);
        });
      }

      markersRef.current.set(markerData.id, marker);
    });
  }, [map, markers, selectedMarkerId, onMarkerClick]);

  //    
  useEffect(() => {
    if (map && selectedMarkerId) {
      const marker = markersRef.current.get(selectedMarkerId);
      if (marker) {
        const position = marker.getPosition();
        if (position) {
          map.panTo(position);
          map.setZoom(17); //  
        }
      }
    }
  }, [map, selectedMarkerId]);

  //  
  if (error) {
    return (
      <div
        style={{
          width: '100%',
          height,
          backgroundColor: '#fff3cd',
          border: '2px solid #ffeaa7',
          borderRadius: '12px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
          padding: '20px',
          color: '#856404',
        }}
      >
        <div style={{ fontSize: '32px', marginBottom: '15px' }}></div>
        <div style={{ fontSize: '16px', textAlign: 'center', fontWeight: 'bold', marginBottom: '8px' }}>
             
        </div>
        <div style={{ fontSize: '14px', textAlign: 'center', color: '#666' }}>
          {error}
        </div>
      </div>
    );
  }

  //  
  if (!mapLoaded) {
    return (
      <div
        style={{
          width: '100%',
          height,
          backgroundColor: '#f8f9fa',
          border: '2px solid #e9ecef',
          borderRadius: '12px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
        }}
      >
        <div style={{ fontSize: '32px', marginBottom: '15px' }}></div>
        <div style={{ fontSize: '16px', color: '#6c757d' }}>  ...</div>
      </div>
    );
  }

  //  
  return (
    <div
      ref={mapRef}
      className={className}
      style={{
        width: '100%',
        height,
        borderRadius: '12px',
        overflow: 'hidden',
        boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
      }}
    />
  );
};

export default GoogleMapComponent;

